import React, {useContext} from 'react'
import WeatherContext from '../contex/contex'
import moment from 'moment'
import './stats.css'

const Stats = () => {
    const {weatherData} = useContext(WeatherContext)
    if(!Object.keys(weatherData).length) {
        return null
    }

    const {pressure, humidity} = weatherData.main
    const {sunrise, sunset} = weatherData.sys
    const windSpeed = weatherData.wind.speed
    const clouds = weatherData.clouds.all


    let calcSunrise = moment.unix(sunrise)
    let formatSunrise = calcSunrise.format('HH.mm')

    let calcSunset = moment.unix(sunset)
    let formatSunset = calcSunset.format('HH.mm')


    return (
        <div className='stats-wrap'>
            <div className="stats-box">
                <div className='stats-item'>
                    <div>{humidity}%</div>
                    <div className='stats-desc'> –í–ª–∞–∂–Ω–æ—Å—Ç—å</div>
                </div>
                <div className='stats-item'>
                    <div>{windSpeed} –º/c</div>
                    <div className='stats-desc'> –í–µ—Ç–µ—Ä</div>
                </div>
                <div className='stats-item'>
                    <div>{formatSunrise} </div>
                    <div className='stats-desc'> –í–æ—Å—Ö–æ–¥</div>
                </div>
            </div>
            <div className="stats-box">
                <div className='stats-item'>
                    <div> {clouds} %</div>
                    <div className='stats-desc'> –û–±–ª–∞—á–Ω–æ—Å—Ç—å </div>
                </div>
                <div className='stats-item'>
                    <div>{pressure} –≥–ü–∞</div>
                    <div className='stats-desc'> –î–∞–≤–ª–µ–Ω–∏–µ</div>
                </div>
                <div className='stats-item'>
                    <div>{formatSunset} </div>
                    <div className='stats-desc'> –ó–∞—Ö–æ–¥</div>
                </div>
            </div>
        </div>
    )
}

export default Stats