import React, {useContext, useEffect} from 'react'
import WeatherContext from '../contex/contex'
import './temperature.css'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faCloudSun, faSun, faSmog, faSnowflake, faCloudRain, faTemperatureLow} from '@fortawesome/free-solid-svg-icons'


const Temperature = () => {
    const {weatherData} = useContext(WeatherContext)
    if(!Object.keys(weatherData).length) {
        return null
    }
    const {temp} = weatherData.main
    const description = weatherData.weather[0].description

    const Capitalize = (description) => {
        return description.charAt(0).toUpperCase() + description.slice(1)
    }

    const icon = () => {
        switch (description) {
            case '—è—Å–Ω–æ':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faSun}/>
                break
            case '–Ω–µ–±–æ–ª—å—à–∞—è –æ–±–ª–∞—á–Ω–æ—Å—Ç—å':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faSun}/>
                break
            case '–æ–±–ª–∞—á–Ω–æ —Å –ø—Ä–æ—è—Å–Ω–µ–Ω–∏—è–º–∏':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faCloudSun}/>
                break
            case '–ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è –æ–±–ª–∞—á–Ω–æ—Å—Ç—å':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faCloudSun}/>
                break
            case '—Ç—É–º–∞–Ω':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faSmog}/>
                break
            case '–ø–ª–æ—Ç–Ω—ã–π —Ç—É–º–∞–Ω':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faSmog}/>
                break
            case '—Å–Ω–µ–≥':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faSnowflake}/>
                break
            case '–Ω–µ–±–æ–ª—å—à–æ–π —Å–Ω–µ–≥':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faSnowflake}/>
                break
            case '–ø–∞—Å–º—É—Ä–Ω–æ':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faCloudRain}/>
                break
            case '–¥–æ–∂–¥—å':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faCloudRain}/>
            case '–Ω–µ–±–æ–ª—å—à–æ–π –¥–æ–∂–¥—å':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faCloudRain}/>
            case '–≥—Ä–æ–∑–∞':
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faCloudRain}/>
                break
            default:
                return <FontAwesomeIcon
                    className='temp-icon'
                    icon={faTemperatureLow}/>
        }

    }

    return (
        <>
            <div className='temp-wraper'>
                <div
                    className='temp-img'
                >
                    {icon()}
                </div>
                <div
                    className='temp-img '
                >
                    <p
                        className='temp-degree temp-text'
                    >{Math.round(temp)}<span>&deg;</span>
                    </p>
                    <p
                        className='temp-text'>
                        {Capitalize(description)}
                    </p>
                </div>
            </div>
        </>
    )
}

export default Temperature