import React from 'react'
import './forecast-item.scss'
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import {faCloudRain, faCloudSun, faSmog, faSnowflake, faSun, faTemperatureLow} from '@fortawesome/free-solid-svg-icons'
import moment from 'moment'

const ForecastItem = ({unit, dataIs}) => {
    let calcDayMonth = moment.unix(unit.dt)
    let formatcalcDayMonth = calcDayMonth.format('D.M')
    console.log(unit)
    const description = unit.weather[0].description
    const data = (unit) => {
        let date = new Date(unit.dt * 1000);
        return  date
    }



    const icon = () => {
        switch (description) {
            case '—è—Å–Ω–æ':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faSun}/>
                break
            case '–Ω–µ–±–æ–ª—å—à–∞—è –æ–±–ª–∞—á–Ω–æ—Å—Ç—å':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faSun}/>
                break
            case '–æ–±–ª–∞—á–Ω–æ —Å –ø—Ä–æ—è—Å–Ω–µ–Ω–∏—è–º–∏':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faCloudSun}/>
                break
            case '–ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è –æ–±–ª–∞—á–Ω–æ—Å—Ç—å':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faCloudSun}/>
                break
            case '—Ç—É–º–∞–Ω':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faSmog}/>
                break
            case '–ø–ª–æ—Ç–Ω—ã–π —Ç—É–º–∞–Ω':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faSmog}/>
                break
            case '—Å–Ω–µ–≥':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faSnowflake}/>
                break
            case '–Ω–µ–±–æ–ª—å—à–æ–π —Å–Ω–µ–≥':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faSnowflake}/>
                break
            case '–ø–∞—Å–º—É—Ä–Ω–æ':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faCloudRain}/>
                break
            case '–¥–æ–∂–¥—å':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faCloudRain}/>
            case '–Ω–µ–±–æ–ª—å—à–æ–π –¥–æ–∂–¥—å':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faCloudRain}/>
            case '–≥—Ä–æ–∑–∞':
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faCloudRain}/>
                break
            default:
                return <FontAwesomeIcon
                    className='forecast-icon'
                    icon={faTemperatureLow}/>
        }

    }

    return (
        <div
            className='forecast-item'
        >
            <div>
                <p> {dataIs(unit.dt)} </p>
                <p className='forecast-stats-description'>{formatcalcDayMonth}</p>
            </div>
            <div
                className='forecast-item-icon'>
                {icon()}
            </div>
            <div>
                <p> {Math.round(unit.main.temp)} </p>
                <p className='forecast-stats-description'>‚ÑÉ </p>
            </div>
            <div>
                <p> {unit.main.humidity} % </p>
                <p className='forecast-stats-description'> –í–ª–∞–∂–Ω–æ—Å—Ç—å </p>
            </div>
            <div>
                <p> {unit.main.pressure} –≥–ü–∞ </p>
                <p className='forecast-stats-description'> –î–∞–≤–ª–µ–Ω–∏–µ </p>
            </div>
            <div>
                <p> {unit.wind.speed} –º/—Å</p>
                <p className='forecast-stats-description'> –í–µ—Ç–µ—Ä </p>
            </div>
        </div>
    )
}

export default ForecastItem
